<template>
  <div class="search">
    <el-row type="flex" justify="center">
      <el-col :xs="22" :sm="16" :md="12" :lg="10" :xl="12" class="vinput">
        <el-input v-model="searchText" placeholder="" autofocus="true"></el-input>
      </el-col>
    </el-row>
    <el-row type="flex" justify="center">
      <el-col :xs="24" :sm="18" :md="16" :lg="14" :xl="12">
        <article-title-list :articles="articles"/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import ArticleTitleList from "./ArticleTitleList";

export default {
  name: "ArticleSearch",
  data() {
    return {
      searchText: "",
      articles: []
    };
  },

  watch: {
    searchText(newValue) {
      if (newValue == "") {
        this.articles = [];
        return;
      }

      this.$http
        .post("/api/articles", {
          searchText: newValue
        })
        .then(result => {
          this.articles = result.data;
        });
    }
  },

  components: {
    ArticleTitleList
  }
};
</script>

<style>
.vinput .el-input__inner:focus {
  border: none;
}
.vinput .el-input__inner {
  background-color: #f9f9f9;
  box-shadow: 0 3px 9px rgba(0, 0, 0, 0.2);
  border: none;
  margin-bottom: 35px;
  margin-top: 15px;
  line-height: 15px
}
</style>


<style scoped>
.search {
  padding-top: 90px;
}
</style>
