<template>
  <div class="category">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :xs="24" :sm="18" :md="16" :lg="14" :xl="12">
        <h2>Tag Page for the Category "{{cateName}}"</h2>
        <article-title-list :articles="articles"/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import ArticleTitleList from "./ArticleTitleList";

export default {
  name: "ArticleCategory",
  props: ["alias", "cateName"],

  data() {
    return {
      articles: []
    };
  },

  created() {
    this.request(this.alias);
  },

  beforeRouteUpdate(to, from, next) {
    this.request(to.params.alias);
    next();
  },

  methods: {
    request(alias) {
      this.$http
        .post("/api/articles", {
          categoryAlias: alias
        })
        .then(result => {
          this.articles = result.data;
        });
    }
  },

  components: {
    ArticleTitleList
  }
};
</script>

<style scoped>
h2 {
  padding: 0 15px;
  margin-bottom: 40px;
  text-align: center;
  color: #252525;
}
.category {
  padding-top: 90px;
}
</style>
